
import { defineComponent, PropType } from "vue";

export default defineComponent({
  props: {
    selectionList: Array as PropType<string[]>,
    selected: String,
  },
  components: {},
  emits: ["onSelect"],
  setup(_, context) {
    const selectHandler = (selection: string) => {
      context.emit("onSelect", selection);
    };
    return { selectHandler };
  },
});
